.MainReg{ 
    background: #FFF; 
  	border: 2px solid black;
  	box-sizing: border-box;
  	position: absolute;
  	height: 100vh;
  	width: 100%;
}
.TextBlockReg{
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 10pt;
	margin-bottom: 10pt;
}
.ImgCodeRegistration{
	display: block;
  	width: 70%;
  	margin-left: auto;
    margin-right: auto; 
}
.InputReg{
	background-color: #FFF;
	color: #FFF;
	width: inherit;
	height: inherit;
}
.ButtonReg{
	background-color: #6F389F;
	border-color: #6F389F;
	color: #FFF; 
	font-family: 'Century Gothic';
	font-size: 18pt;
}
.ButtonBlockReg{
	margin-right: 18%;
	margin-left: 18%;
}