.MainRecord{ 
    background: #FFF; 
  	border: 2px solid black;
  	box-sizing: border-box;
  	position: absolute;
  	height: 100vh;
  	width: 100%;
}
.TextBlockRecord{
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 10pt;
	margin-bottom: 50pt;
}
.RecordBlock{
	display: block;
	text-align: center;
}
.ButtonMothsDay{
	margin-left: 2%;
	margin-right: 2%;
	background-color: #FFF;
	border-color: #C0C0C0;
	color: #4672C4; 
	font-family: 'Century Gothic';
	margin-bottom: 10pt;
}
.FontClass{
	font-family: 'Century Gothic';
	font-size: 12pt;
	font-weight: bold;
}
.RecordBlockDataTime{
	display: block;
	text-align: center;
}
.ButtonDataTime{
	position: relative;
	margin-left: 4%;
	margin-right: 4%;
	border-color: #000;
	width: 55pt;
	height: 55pt;
	border-radius: 40px;
	background-image: url(../img/VacantDay.png);
	​​​​​​​background-repeat: no-repeat;
	background-size: contain;
}
.TimeRecord{
	width: 40pt;
	height: 9pt;
	display: block;
	border-color: #434342;
	font-family: 'Century Gothic';
	font-size: 10pt;
	color: #FFF; 
}
.CountRecord{
	display: block;
	position: absolute;
	border-color: #000;
	text-align: center;
	font-size: 8pt;
	right: 5%;
	left: 5%;
	bottom: 0.1%;
}

.ButtonBlcokRecord{
	margin-top: 20pt;
	background-color: #6F389F;
	border-color: #6F389F;
	color: #FFF; 
	font-family: 'Century Gothic';
	font-size: 33pt;
}
.ButtonBlocksRecord{
	margin-right: 18%;
	margin-left: 18%;
	margin-bottom: 20pt;
	font-size: 33pt;
}